import { memo } from 'react';
import { Box, Typography, Link } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { userEmailSelector, userIdSelector, userNameSelector } from '../../../user/store/selectors';
import { useNavigate } from 'react-router-dom';
import { Navigation } from '../../../../core/components/navigation';
import { Button } from '../../../../core/components/button';
import { logoutAction } from '../../../user/store/actions';
import { socket } from '../../../../core/utils/socket';

const Profile = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const userEmail = useSelector(userEmailSelector);
  const userName = useSelector(userNameSelector);
  const userId = useSelector(userIdSelector);

  return (
    <Box
      sx={{
        position: 'fixed',
        top: '0',
        left: '0',
        display: 'grid',
        gridTemplateColumns: 'max-content 1fr',
        width: '100%',
        height: '100%',
        alignContent: 'start',
      }}
    >
      <Navigation />
      <Box>
        <Typography variant="h5"> email: {userEmail}</Typography>
        <Typography variant="h5"> : {userName}</Typography>
        {/* TODO:   (   ) */}
        <Typography variant="h5"> :</Typography>
        {/* TODO:   */}
        <Typography variant="h5">:</Typography>
        {/* TODO:    */}
        <Typography variant="h5"> :</Typography>
        {/* TODO:   */}
        <Typography variant="h5"> :</Typography>
        <Typography variant="h5">
          <Link sx={{ cursor: 'pointer' }} underline="hover" onClick={() => navigate(-1)}>
            
          </Link>
          <Button
            variant="contained"
            color="primary"
            onClick={() => {
              dispatch(logoutAction());

              socket.emit('on-disconnect', userId);
            }}
          >
              
          </Button>
        </Typography>
      </Box>
    </Box>
  );
};

export const ProfileMemoized = memo(Profile);
